import React from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import { MdComputer } from "react-icons/md";
import { Link } from "react-router-dom";

import "../../styles/styles.css";

const Navigation = () => {
  return (
    <Navbar
      collapseOnSelect
      expand="lg"
      variant="dark"
      className="Navbar-styles"
    >
      <MdComputer style={{ marginRight: "1em" }} />
      <Navbar.Brand href="#home">Fernando Correia</Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto"></Nav>
        <Nav>
          <Nav.Link>
            <Link className="Navbar-Tabs" to="/home">
              Home
            </Link>
          </Nav.Link>
          <Nav.Link>
            <Link className="Navbar-Tabs" to="/about">
              About
            </Link>
          </Nav.Link>
          <Nav.Link>
            <Link className="Navbar-Tabs" to="/resume">
              Resume
            </Link>
          </Nav.Link>
          <Nav.Link>
            <Link className="Navbar-Tabs" to="/projects">
              Projects
            </Link>
          </Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default Navigation;
